.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

.resetMapButton {
  margin-left: 1px;
  margin-right: 1px;
  margin-bottom: 1px;
  height: 27px;
  font-size: 16px;
  color: white;
  clear: both;
  border: 1px solid #2E2F2F;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#4F5151), color-stop(6%, #474949), to(#3F4141));
  background-image: -webkit-linear-gradient(#4F5151, #474949 6%, #3F4141);
  background-image: -o-linear-gradient(#4F5151, #474949 6%, #3F4141);
  background-image: linear-gradient(#4F5151, #474949 6%, #3F4141);
  background-color: #474949;
  border-color: #474949;
  line-height: 1.42857143;
  border-radius: 4px;
  text-align: center;
  cursor: pointer;
}

.resetMapButton:active {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#3F4141), color-stop(6%, #474949), to(#4F5151));
  background-image: -webkit-linear-gradient(#3F4141, #474949 6%, #4F5151);
  background-image: -o-linear-gradient(#3F4141, #474949 6%, #4F5151);
  background-image: linear-gradient(#3F4141, #474949 6%, #4F5151);
}

.bottomPanel {
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  width: 100%;
  bottom: 0;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@media (max-width: 405px) {
  .bottomPanel {
    -ms-flex-direction: column;
        flex-direction: column;
  }
}



