.button {
  color: white;
  border: 1px solid #2E2F2F;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#4F5151), color-stop(6%, #474949), to(#3F4141));
  background-image: -webkit-linear-gradient(#4F5151, #474949 6%, #3F4141);
  background-image: -o-linear-gradient(#4F5151, #474949 6%, #3F4141);
  background-image: linear-gradient(#4F5151, #474949 6%, #3F4141);
  background-color: white;
  border-color: #474949;
  line-height: 1;
  border-radius: 4px;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  font-size: 90%;
  font-weight: 500;
  height: 23px;
}

.button:active {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#3F4141), color-stop(6%, #474949), to(#4F5151));
  background-image: -webkit-linear-gradient(#3F4141, #474949 6%, #4F5151);
  background-image: -o-linear-gradient(#3F4141, #474949 6%, #4F5151);
  background-image: linear-gradient(#3F4141, #474949 6%, #4F5151);
}

.button:disabled {
  color: gray;
  background-color: #D8D8D8;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#D8D8D8), to(#D8D8D8));
  background-image: -webkit-linear-gradient(#D8D8D8, #D8D8D8);
  background-image: -o-linear-gradient(#D8D8D8, #D8D8D8);
  background-image: linear-gradient(#D8D8D8, #D8D8D8);
  cursor: not-allowed;
}

.input {
  line-height: 1;
  border-radius: 4px;
  vertical-align: middle;
  font-size: 90%;
  height: 23px;
  color: black;
  background-color: #F0F0F0;
  border: 1px solid #797979;
  width: 47%;
  text-align: center;
  cursor: text;
}



